<template>
  <div class="record-form">
    <el-card>
      <template #header>
        <div class="card-header">
          <h3>{{ isEdit ? '编辑记录' : '创建记录' }}</h3>
          <div class="header-actions">
            <el-button @click="handleCancel">取消</el-button>
            <el-button
              type="primary"
              :loading="saving"
              @click="handleSave"
            >
              {{ isEdit ? '更新' : '创建' }}
            </el-button>
          </div>
        </div>
      </template>

      <el-form
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="100px"
        class="record-form-content"
      >
        <!-- 基础信息 -->
        <el-row :gutter="20">
          <el-col :xs="24" :md="12">
            <el-form-item label="记录类型" prop="type">
              <el-select
                v-model="form.type"
                placeholder="选择记录类型"
                @change="handleTypeChange"
                style="width: 100%"
              >
                <el-option
                  v-for="type in recordTypes"
                  :key="type.id"
                  :label="type.name"
                  :value="type.name"
                />
              </el-select>
            </el-form-item>
          </el-col>
          
          <el-col :xs="24" :md="12">
            <el-form-item label="状态" prop="status">
              <el-select v-model="form.status" placeholder="选择状态" style="width: 100%">
                <el-option label="草稿" value="draft" />
                <el-option label="已发布" value="published" />
                <el-option label="已归档" value="archived" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="标题" prop="title">
          <el-input
            v-model="form.title"
            placeholder="请输入记录标题"
            maxlength="200"
            show-word-limit
          />
        </el-form-item>

        <!-- 动态字段 -->
        <div v-if="selectedRecordType" class="dynamic-fields">
          <h4>记录内容</h4>
          <div
            v-for="field in selectedRecordType.fields"
            :key="field.name"
            class="dynamic-field"
          >
            <el-form-item
              :label="field.label"
              :prop="`content.${field.name}`"
              :rules="getDynamicFieldRules(field)"
            >
              <!-- 文本输入 -->
              <el-input
                v-if="field.type === 'text'"
                v-model="form.content[field.name]"
                :placeholder="`请输入${field.label}`"
              />
              
              <!-- 多行文本 -->
              <el-input
                v-else-if="field.type === 'textarea'"
                v-model="form.content[field.name]"
                type="textarea"
                :rows="4"
                :placeholder="`请输入${field.label}`"
              />
              
              <!-- 数字输入 -->
              <el-input-number
                v-else-if="field.type === 'number'"
                v-model="form.content[field.name]"
                :placeholder="`请输入${field.label}`"
                style="width: 100%"
              />
              
              <!-- 日期选择 -->
              <el-date-picker
                v-else-if="field.type === 'date'"
                v-model="form.content[field.name]"
                type="date"
                :placeholder="`请选择${field.label}`"
                style="width: 100%"
              />
              
              <!-- 下拉选择 -->
              <el-select
                v-else-if="field.type === 'select'"
                v-model="form.content[field.name]"
                :placeholder="`请选择${field.label}`"
                style="width: 100%"
              >
                <el-option
                  v-for="option in field.options"
                  :key="option"
                  :label="option"
                  :value="option"
                />
              </el-select>
              
              <!-- 文件上传 -->
              <div v-else-if="field.type === 'file'" class="file-upload">
                <el-upload
                  :action="uploadAction"
                  :headers="uploadHeaders"
                  :on-success="(response) => handleFileSuccess(response, field.name)"
                  :on-error="handleFileError"
                  :before-upload="beforeFileUpload"
                  :file-list="getFileList(field.name)"
                  multiple
                >
                  <el-button type="primary">
                    <el-icon><Upload /></el-icon>
                    上传文件
                  </el-button>
                  <template #tip>
                    <div class="el-upload__tip">
                      支持常见文件格式，单个文件不超过 10MB
                    </div>
                  </template>
                </el-upload>
              </div>
            </el-form-item>
          </div>
        </div>

        <!-- OCR识别区域 -->
        <el-card v-if="showOCR" class="ocr-card" shadow="never">
          <template #header>
            <div class="flex-between">
              <span>OCR文字识别</span>
              <el-button size="small" @click="showOCR = false">
                <el-icon><Close /></el-icon>
              </el-button>
            </div>
          </template>
          
          <el-upload
            :action="ocrUploadAction"
            :headers="uploadHeaders"
            :on-success="handleOCRSuccess"
            :on-error="handleOCRError"
            :before-upload="beforeOCRUpload"
            drag
            accept="image/*"
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              将图片拖到此处，或<em>点击上传</em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                支持 jpg/png/gif 格式，文件大小不超过 5MB
              </div>
            </template>
          </el-upload>
          
          <div v-if="ocrResult" class="ocr-result">
            <h5>识别结果：</h5>
            <el-input
              v-model="ocrResult.text"
              type="textarea"
              :rows="6"
              placeholder="OCR识别结果将显示在这里"
            />
            <div class="ocr-actions">
              <el-button @click="insertOCRResult">插入到内容</el-button>
              <el-button @click="ocrResult = null">清除</el-button>
            </div>
          </div>
        </el-card>

        <!-- 操作按钮 -->
        <div class="form-actions">
          <el-button @click="showOCR = !showOCR">
            <el-icon><Camera /></el-icon>
            OCR识别
          </el-button>
          
          <el-button @click="handlePreview">
            <el-icon><View /></el-icon>
            预览
          </el-button>
          
          <el-button type="primary" :loading="saving" @click="handleSave">
            <el-icon><Check /></el-icon>
            {{ isEdit ? '更新' : '保存' }}
          </el-button>
        </div>
      </el-form>
    </el-card>

    <!-- 预览对话框 -->
    <el-dialog
      v-model="previewVisible"
      title="记录预览"
      width="80%"
      top="5vh"
    >
      <div class="preview-content">
        <h2>{{ form.title }}</h2>
        <div class="preview-meta">
          <el-tag>{{ form.type }}</el-tag>
          <el-tag :type="getStatusType(form.status)">
            {{ getStatusText(form.status) }}
          </el-tag>
        </div>
        
        <div v-if="selectedRecordType" class="preview-fields">
          <div
            v-for="field in selectedRecordType.fields"
            :key="field.name"
            class="preview-field"
          >
            <label>{{ field.label }}：</label>
            <div class="field-value">
              {{ formatFieldValue(field, form.content[field.name]) }}
            </div>
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed, onMounted, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ElMessage, ElMessageBox, type FormInstance, type FormRules } from 'element-plus'
import { useAuthStore } from '@/stores/auth'
import { http } from '@/utils/request'
import { API_ENDPOINTS } from '@/config/api'
import type { Record, RecordType, RecordField, FileInfo, OCRResponse } from '@/types'

const route = useRoute()
const router = useRouter()
const authStore = useAuthStore()

// 响应式数据
const formRef = ref<FormInstance>()
const saving = ref(false)
const showOCR = ref(false)
const previewVisible = ref(false)
const recordTypes = ref<RecordType[]>([])
const ocrResult = ref<OCRResponse | null>(null)

// 表单数据
const form = reactive({
  type: '',
  title: '',
  status: 'draft',
  content: {} as Record<string, any>
})

// 计算属性
const isEdit = computed(() => !!route.params.id)
const recordId = computed(() => Number(route.params.id))

const selectedRecordType = computed(() => {
  return recordTypes.value.find(type => type.name === form.type)
})

const uploadAction = computed(() => API_ENDPOINTS.FILES.UPLOAD)
const ocrUploadAction = computed(() => API_ENDPOINTS.FILES.OCR)
const uploadHeaders = computed(() => ({
  Authorization: `Bearer ${authStore.token}`
}))

// 表单验证规则
const formRules: FormRules = {
  type: [
    { required: true, message: '请选择记录类型', trigger: 'change' }
  ],
  title: [
    { required: true, message: '请输入标题', trigger: 'blur' },
    { min: 1, max: 200, message: '标题长度在 1 到 200 个字符', trigger: 'blur' }
  ],
  status: [
    { required: true, message: '请选择状态', trigger: 'change' }
  ]
}

// 方法
const getDynamicFieldRules = (field: RecordField) => {
  const rules: any[] = []
  
  if (field.required) {
    rules.push({
      required: true,
      message: `请输入${field.label}`,
      trigger: field.type === 'select' ? 'change' : 'blur'
    })
  }
  
  if (field.validation) {
    // 可以根据字段配置添加更多验证规则
    if (field.validation.min !== undefined) {
      rules.push({
        min: field.validation.min,
        message: `${field.label}最少${field.validation.min}个字符`,
        trigger: 'blur'
      })
    }
    
    if (field.validation.max !== undefined) {
      rules.push({
        max: field.validation.max,
        message: `${field.label}最多${field.validation.max}个字符`,
        trigger: 'blur'
      })
    }
  }
  
  return rules
}

const getStatusType = (status: string) => {
  const statusMap: Record<string, string> = {
    draft: 'info',
    published: 'success',
    archived: 'warning'
  }
  return statusMap[status] || 'info'
}

const getStatusText = (status: string) => {
  const statusMap: Record<string, string> = {
    draft: '草稿',
    published: '已发布',
    archived: '已归档'
  }
  return statusMap[status] || status
}

const formatFieldValue = (field: RecordField, value: any) => {
  if (value === null || value === undefined || value === '') {
    return '未填写'
  }
  
  if (field.type === 'file' && Array.isArray(value)) {
    return value.map(file => file.originalName).join(', ')
  }
  
  return String(value)
}

const getFileList = (fieldName: string) => {
  const files = form.content[fieldName]
  if (!Array.isArray(files)) return []
  
  return files.map((file: FileInfo) => ({
    name: file.originalName,
    url: file.path,
    uid: file.id
  }))
}

// 加载记录类型
const loadRecordTypes = async () => {
  try {
    const response = await http.get<RecordType[]>(API_ENDPOINTS.RECORD_TYPES.LIST)
    recordTypes.value = response
  } catch (error) {
    console.error('Failed to load record types:', error)
    ElMessage.error('加载记录类型失败')
  }
}

// 加载记录详情（编辑模式）
const loadRecord = async () => {
  if (!isEdit.value) return
  
  try {
    const response = await http.get<Record>(API_ENDPOINTS.RECORDS.DETAIL(recordId.value))
    
    form.type = response.type
    form.title = response.title
    form.status = response.status
    form.content = response.content || {}
    
  } catch (error) {
    console.error('Failed to load record:', error)
    ElMessage.error('加载记录失败')
    router.push('/records')
  }
}

// 处理记录类型变更
const handleTypeChange = (type: string) => {
  const recordType = recordTypes.value.find(t => t.name === type)
  if (!recordType) return
  
  // 初始化动态字段的默认值
  const newContent: Record<string, any> = {}
  recordType.fields.forEach(field => {
    if (form.content[field.name] !== undefined) {
      newContent[field.name] = form.content[field.name]
    } else {
      switch (field.type) {
        case 'number':
          newContent[field.name] = 0
          break
        case 'file':
          newContent[field.name] = []
          break
        default:
          newContent[field.name] = ''
      }
    }
  })
  
  form.content = newContent
}

// 文件上传处理
const beforeFileUpload = (file: File) => {
  const isLt10M = file.size / 1024 / 1024 < 10
  
  if (!isLt10M) {
    ElMessage.error('文件大小不能超过 10MB')
    return false
  }
  
  return true
}

const handleFileSuccess = (response: any, fieldName: string) => {
  if (!Array.isArray(form.content[fieldName])) {
    form.content[fieldName] = []
  }
  
  form.content[fieldName].push(response.file)
  ElMessage.success('文件上传成功')
}

const handleFileError = (error: any) => {
  console.error('File upload failed:', error)
  ElMessage.error('文件上传失败')
}

// OCR处理
const beforeOCRUpload = (file: File) => {
  const isImage = file.type.startsWith('image/')
  const isLt5M = file.size / 1024 / 1024 < 5
  
  if (!isImage) {
    ElMessage.error('只能上传图片文件')
    return false
  }
  
  if (!isLt5M) {
    ElMessage.error('图片大小不能超过 5MB')
    return false
  }
  
  return true
}

const handleOCRSuccess = (response: OCRResponse) => {
  ocrResult.value = response
  ElMessage.success('OCR识别完成')
}

const handleOCRError = (error: any) => {
  console.error('OCR failed:', error)
  ElMessage.error('OCR识别失败')
}

const insertOCRResult = () => {
  if (!ocrResult.value || !selectedRecordType.value) return
  
  // 找到第一个文本类型的字段并插入OCR结果
  const textField = selectedRecordType.value.fields.find(
    field => field.type === 'text' || field.type === 'textarea'
  )
  
  if (textField) {
    const currentValue = form.content[textField.name] || ''
    form.content[textField.name] = currentValue + (currentValue ? '\n' : '') + ocrResult.value.text
    ElMessage.success('OCR结果已插入')
  } else {
    ElMessage.warning('没有找到可插入的文本字段')
  }
}

// 操作处理
const handlePreview = () => {
  previewVisible.value = true
}

const handleSave = async () => {
  if (!formRef.value) return
  
  try {
    await formRef.value.validate()
    
    saving.value = true
    
    const data = {
      type: form.type,
      title: form.title,
      status: form.status,
      content: form.content
    }
    
    if (isEdit.value) {
      await http.put(API_ENDPOINTS.RECORDS.UPDATE(recordId.value), data)
      ElMessage.success('记录更新成功')
    } else {
      await http.post(API_ENDPOINTS.RECORDS.CREATE, data)
      ElMessage.success('记录创建成功')
    }
    
    router.push('/records')
    
  } catch (error: any) {
    console.error('Save failed:', error)
    ElMessage.error(error.message || '保存失败')
  } finally {
    saving.value = false
  }
}

const handleCancel = async () => {
  try {
    await ElMessageBox.confirm('确定要取消吗？未保存的更改将丢失。', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
    
    router.push('/records')
  } catch (error) {
    // 用户取消操作
  }
}

// 生命周期
onMounted(async () => {
  await loadRecordTypes()
  if (isEdit.value) {
    await loadRecord()
  }
})

// 监听路由变化
watch(() => route.params.id, () => {
  if (isEdit.value) {
    loadRecord()
  } else {
    // 重置表单
    Object.assign(form, {
      type: '',
      title: '',
      status: 'draft',
      content: {}
    })
  }
})
</script>

<style scoped>
.record-form {
  padding: 0;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-header h3 {
  margin: 0;
  font-size: 18px;
  color: #303133;
}

.header-actions {
  display: flex;
  gap: 8px;
}

.record-form-content {
  padding: 20px 0;
}

.dynamic-fields {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid #ebeef5;
}

.dynamic-fields h4 {
  margin: 0 0 20px 0;
  font-size: 16px;
  color: #303133;
}

.dynamic-field {
  margin-bottom: 20px;
}

.file-upload {
  width: 100%;
}

.ocr-card {
  margin: 20px 0;
}

.ocr-result {
  margin-top: 20px;
}

.ocr-result h5 {
  margin: 0 0 12px 0;
  font-size: 14px;
  color: #303133;
}

.ocr-actions {
  margin-top: 12px;
  display: flex;
  gap: 8px;
}

.form-actions {
  margin-top: 32px;
  padding-top: 20px;
  border-top: 1px solid #ebeef5;
  display: flex;
  gap: 12px;
  justify-content: center;
}

.preview-content {
  padding: 20px;
}

.preview-content h2 {
  margin: 0 0 16px 0;
  font-size: 24px;
  color: #303133;
}

.preview-meta {
  margin-bottom: 24px;
  display: flex;
  gap: 8px;
}

.preview-fields {
  margin-top: 20px;
}

.preview-field {
  margin-bottom: 16px;
  display: flex;
  align-items: flex-start;
}

.preview-field label {
  min-width: 120px;
  font-weight: 500;
  color: #606266;
}

.field-value {
  flex: 1;
  color: #303133;
  word-break: break-all;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .card-header {
    flex-direction: column;
    gap: 16px;
    align-items: stretch;
  }
  
  .header-actions {
    justify-content: center;
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .preview-field {
    flex-direction: column;
    gap: 4px;
  }
  
  .preview-field label {
    min-width: auto;
  }
}
</style>